<template>
  <div id="app">
    <div id="top-bar" ref="topBar">
      <router-link id="mobile-logo-link" to="/">
        <img
          alt="mobile-logo"
          src="@/assets/logo-optyka-mobile.png"
          class="mobile-logo"
          @load="setMargin"
        />
      </router-link>
      <button
        id="mobile-open"
        class="hamburger hamburger--spring"
        :class="{ 'is-active': isActive }"
        type="button"
        @click="moveNav"
      >
        <span class="hamburger-box">
          <span class="hamburger-inner"></span>
        </span>
      </button>
    </div>
    <div id="mobile-nav" class="mobile-nav" ref="mobileNav">
      <router-link v-on:click.native="close()" class="side-link" to="/">HOME</router-link>
      <router-link v-on:click.native="close()" class="side-link" to="/badania">BADANIA</router-link>
      <router-link v-on:click.native="close()" class="side-link" to="/oferta">OFERTA</router-link>
      <router-link v-on:click.native="close()" class="side-link" to="/zespol">ZESPÓŁ</router-link>
    </div>
    <div id="page-content" ref="pageContent">
      <router-view />
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data: function() {
    return {
      isActive: false,
      imgToLoad: '@/assets/logo-optyka-mobile.png',
    }
  },
  methods: {
    moveNav() {
      if (this.isActive) {
        this.$refs.mobileNav.style.width = '0'
        document.getElementById('page-content').style.right = '0'
        this.isActive = false
      } else {
        this.$refs.mobileNav.style.width = '50vw'
        document.getElementById('page-content').style.right = '50vw'
        this.isActive = true
      }
    },
    setMargin() {
      const topBar = document.querySelector('#top-bar')
      document.body.style.marginTop = `${topBar.scrollHeight}px`
    },
    close() {
      this.$refs.mobileNav.style.width = '0'
      document.getElementById('page-content').style.right = '0'
      this.isActive = false
    },
  },
  created() {
    window.onresize = () => this.setMargin()
  },
}
</script>

