  #top-bar {
    display: block;
  }
}

#mobile-logo-link {
  text-align: center;
}

#mobile-logo-link img {
  max-width: 30vw;
  padding-bottom: 1vh;

  display: block;
  margin-left: auto;
  margin-right: auto;
}

#mobile-open {
  display: block;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  right: 0;
}

#mobile-nav {
  height: 100vh;
  width: 0;
  right: 0;

  display: flex;
  flex-direction: column;
  justify-items: center;

  position: fixed;
  z-index: 10;
  overflow-x: hidden; /* Disable horizontal scroll */
  padding-top: 5%;

  background-color: #ffffff;
  transition: 0.75s ease-in-out;
}

.mobile-nav .side-link {
  padding: 2vh 0 2vh 0;
  margin-left: 5vw;
  margin-right: 5vw;
  text-decoration: none;
  text-align: center;
  font-style: normal;
  font-weight: 300;
  font-size: 3.5em;
  color: #000000;
  display: block;
  transition: 0.75s;
}

.mobile-nav .side-link:hover {
  color: #34c231;
}

.mobile-nav .side-link:active {
  color: #ffffff;
  background: #34c231;
}

.side-link::after {
  content: '';
  position: relative;
  left: 0;
  bottom: -2vh;
  width: 100%;
  height: 2px;
  background-color: #34c231;
  opacity: 1;
  display: block;
}

.mobile-nav .side-link:last-child::after {
  display: none;
}
/* mobile navigation end */

#page-content {
  position: absolute;
  right: 0;
  transition: right 0.75s;
  overflow: hidden;
}
</style>
