import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior: function(to, from, savedPosition) {
    if (to.hash) {
      return { selector: to.hash }
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('@/views/Home.vue'),
    },
    {
      path: '/badania',
      name: 'badania',
      component: () => import('@/views/Badania.vue'),
    },
    {
      path: '/oferta',
      name: 'oferta',
      component: () => import('@/views/Oferta.vue'),
    },
    {
      path: '/zespol',
      name: 'zespol',
      component: () => import('@/views/Zespol.vue'),
    },
  ],
})
